import {
  getSeries
} from './chartsOptionsUtil'
import _color from './color'
export default (data, title, average) => {
  var dataMap = data.dataMap;
  var legend = data.indexNameList;
  var years = data.yearList;
  var xAxis = data.cityList;
  const indexAverageMap = data.indexAverageMap;

  var legendSeries = [];
  for (let i in legend) {
    legendSeries.push({
      name: legend[i],
      type: 'bar',
      itemStyle: {
        color: _color[i]
      },
      markLine: {
        name: '平均值',
        label: {
          normal: {
            show: true,
            position: 'end',
            formatter: '{b}{c}'
          }
        },
        lineStyle: {
          color: _color[i]
        },
        data: [{
          name: '平均值为:',
          yAxis: average[legend[i]]
        }]
      }
    });
  }

  var options = [];

  for (var i in years) {
    options.push({
      title: {
        text: years[i] + '年 ' + title
      },
      series: getSeries(years[i], legend, dataMap)
    });
  }

  return {
    baseOption: {
      // dataZoom: [{
      //     show: true,
      //     top: '30'
      //   },
      //   {
      //     type: 'inside'
      //   },
      // ],
      timeline: {
        axisType: 'category',
        autoPlay: true,
        playInterval: 5000,
        data: years,
        label: {
          formatter: function (s) {
            return s;
          }
        }
      },
      title: {
        left: 'center'
      },
      tooltip: {},
      color: _color,
      legend: {
        top: 60,
        x: 'center',
        data: legend,
        width: "70%"
      },
      calculable: true,
      toolbox: {
        bottom: 80,
        right: 20,
        orient: 'vertical',
        itemSize: 20,
        itemGap: 15,
        feature: {
          restore: {},
          magicType: {
            type: ['bar']
          },
          saveAsImage: {}
        }
      },
      grid: {
        top: 200,
        bottom: 80,
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow',
            label: {
              show: true,
              formatter: function (params) {
                return params.value.replace('\n', '');
              }
            }
          }
        }
      },
      xAxis: [{
        'type': 'category',
        'axisLabel': {
          'interval': 0
        },
        'data': xAxis,
        splitLine: {
          show: false
        }
      }],
      yAxis: [{
        type: 'value',
        name: legend[0].split('(')[1].split(')')[0]
      }],
      series: legendSeries
    },
    options: options
  };
}



