<template>
    <div class="charts-template">
        <template v-for="(item,index) in chartListData">
            <div :ref="`charts${index}`" :id="`charts${index}`" class="charts-box" :key="index"></div>
            <el-collapse :accordion="true" :key="index+10">
                <h3 class="result">数据分析&emsp;<Button @click="value4 = true" size="small" type="dashed">查看指标信息</Button></h3>
                <el-collapse-item v-for="i in allInfo" :key="i.year">
                    <template slot="title">
                    {{ i.year }} 年
                    </template>
                    <h4 v-if="item.cityList.length > 1">指标分析</h4>
                    <template v-for="(p, q) in i.indexInfo" v-if="item.cityList.length > 1">
                        <div :key="q" style="text-indent: 14px; color: #888;">
                            <span style="color: #000">{{p.indexName}}</span>：最高分是<span style="color: #000">{{p.topName}}</span><span style="color: #f65c5c;"> ( {{p.topScore}} 分) </span>，
                            {{typeTitle}}平均得分为<span style="color: #f65c5c;"> {{p.average}} 分</span>，
                            得分方差为<span style="color: #f65c5c;"> {{p.variance}} </span>，
                            {{typeTitle}}之间得分<span style="color: #000">{{p.variance/p.grossScore-0.2 > 0 ? '差距较大':'较为均衡'}}</span>。
                        </div>
                    </template>
                    <h4>地区分析</h4>
                     <template v-for="(k, h) in i.otherInfo">
                        <div :key="h+100" style="text-indent: 14px; color: #888;">
                            <span style="color: #000">{{k.cityName}}</span>：各指标及得分为
                            <template v-for="(m, n) in k.indexList">
                                <span :key="n+200"><span  style="color: #000">{{m.indexInfo}}<span style="color: #f65c5c;"> ( {{m.indexScore}} 分 ) </span></span></span>，
                            </template>
                            指标得分<span style="color: #000">{{ i.allTotalAverage * 0.2 >= k.number ? '相对稳定':(k.number > i.allTotalAverage * 0.2 && i.allTotalAverage * 0.4 >= k.number? '波动较小':'差别明显' )}}</span>。
                        </div>
                    </template>
                </el-collapse-item>
            </el-collapse>
        </template>
        <Drawer :closable="false" width="30" v-model="value4">
            <h2>指标信息</h2>
            <template v-for="(item,index) in chartListData">
                <div :key="index">
                    <template v-for="(i,k) in item.indexInfos">
                        <div :key="k+20" class="data-list">
                            <p class="data-text">指标名称：<span>{{i.indexName}}</span></p>
                            <p class="data-text">指标描述：<span>{{i.description}}</span></p>
                            <p class="data-text">评分标准：<span>{{i.evaluationStandards}}</span></p>
                        </div>
                    </template>  
                </div>
            </template>
        </Drawer>
    </div>
</template>

<script>
    import echarts from 'echarts'
    import 'echarts/map/js/china.js'
    import 'echarts/theme/macarons.js'
    import createOptions from '../data/chartsOptions.js'
    export default {
        data() {
            return {
                chartTitle: '',
                typeTitle: '',
                allInfo: [],
                dataHandle: {},
                value4: false,
                pStyle: {
                    fontSize: '16px',
                    color: 'rgba(0,0,0,0.85)',
                    lineHeight: '24px',
                    display: 'block',
                    marginBottom: '16px'
                }
            }
        },
        props: ['chartListData'],
        created() {
            this.chartTitle = '地区指标分析图'
            this.typeTitle = '地区'
        },
        mounted() {
            this.$nextTick(() => {
                this.init();
            })
        },
        methods: {
            initmap(data, i) {
                const node = document.getElementById(`charts${i}`);
                const myChart = echarts.init(node, 'macarons');
                myChart.setOption(createOptions(data, this.chartTitle, data.indexAverageMap[data.yearList[0]]));
                myChart.on('timelinechanged', (params) => {
                    myChart.setOption(createOptions(data, this.chartTitle, data.indexAverageMap[data.yearList[params.currentIndex]]));
                });
            },
            // 计算处理分析数据
            handleData(data) {
                // console.log(data)
                let obj = {} , sum = null
                let indexCityData = data.indexCityData
                let yearList = data.yearList
                let indexAverageMap = data.indexAverageMap
                let dataMap = data.dataMap
                let otherInfoList = data.otherInfoList
                otherInfoList.forEach((el,k) => {
                    obj[el.year] = el.otherInfo
                })
                let infoList = Object.assign(obj)
                let indexInfos = data.indexInfos
                indexInfos.forEach((el, index) => {
                    sum += el.grossScore
                })
                // console.log(sum)
                yearList.forEach((e, i) => {
                    this.dataHandle = {}
                    let arr = []
                    this.dataHandle.year = e
                    this.dataHandle.infoOne = dataMap[e]
                    this.dataHandle.otherInfo = infoList[e]
                    Object.keys(indexCityData).forEach((k, j) => {
                        let obj = {}
                        obj.indexName = k
                        arr.push(obj)
                        this.dataHandle.indexInfo = arr
                    })
                    // console.log(this.dataHandle)
                    this.allInfo.push(this.dataHandle)
                })
                this.allInfo.forEach((el, i) => {
                    // console.log(el)
                    el.allTotalAverage = sum / indexInfos.length
                    el.indexInfo.forEach((k, h) => {
                        indexInfos.forEach((p, q) => {
                            if(p.indexName === k.indexName){
                                el.indexInfo[h].grossScore = p.grossScore
                            }
                        })
                        el.indexInfo[h].cityInfo = el.infoOne[k.indexName]  // cityInfo
                        let list = el.infoOne[k.indexName].map(item => Number(item.value))
                        let topScore = list.sort((a,b) => {return a-b})
                        topScore.reverse()
                        el.indexInfo[h].topScore = topScore[0]  // topScore
                        let topCityList = el.infoOne[k.indexName].filter(item => item.value === String(topScore[0])).map(item => item.cityName)
                        // console.log(topCityList)
                        el.indexInfo[h].topName = topCityList.join('、')  // topName
                        let sum = 0, sumS = 0
                        for(var i = 0; i < list.length; i++){
                            sum += list[i]
                        }
                        for(var i = 0; i < list.length; i++){
                            sumS += Math.pow(list[i] - sum / list.length , 2);
                        }
                        el.indexInfo[h].average = (sum / list.length).toFixed(2)  // average
                        el.indexInfo[h].variance = (sumS / list.length).toFixed(2)  // variance
                    })
                    el.otherInfo.forEach((m, n) => {
                        let list = m.indexList.map(item => Number(item.indexScore))
                        let topScore = list.sort((a,b) => {return a-b})
                        topScore.reverse()
                        let num = topScore[0]-topScore[topScore.length-1]
                        el.otherInfo[n].number = num
                    })
                })
                // console.log(this.allInfo)
            },
            init() {
                this.chartListData.forEach((e, i) => {
                    this.initmap(e, i);
                    this.handleData(e);
                })
            }
        }
    }
</script>

<style scoped>
    .charts-box {
        width: 100%;
        min-height: 640px;
        margin-top: 20px;
        overflow: hidden;
        padding: 20px 24px 20px;
        border: 1px solid #ebeef5;
    }
    .charts-template .chart-text:last-of-type{
        border-bottom: none;
    }
    .chart-text{
        width: 80%;
        margin: 0 auto;
        box-sizing: border-box;
        padding: 8px 0 10px; 
        border-bottom: 1px solid #333;
    }
    .chart-text li{
        line-height: 1.5;
    }
    .chart-text li span{
        font-weight: 700;
    }
    .el-collapse{
        box-sizing: border-box;
        padding: 10px 80px;
        border: none;
    }
    .result{
        text-align: center;
    }
    .data-list{
        margin-bottom: 20px;
    }
    .data-text{
        font-weight: 700;
        font-size: 14px;
    }
</style>



