export const getSeries = (year, legend, dataMap) => {
  var dataSeries = [];
  for (var j in legend) {
    var param = legend[j];
    var _dM = dataMap[year][param];
    _dM && _dM.map(function (item) {
      (+item.value).toFixed(4);
      if (item.value === null) {
        item.value = '暂无数据';
      }
      item.name = item.cityName;
      return item;
    });
    dataSeries.push({
      data: _dM
    });
  }
  return dataSeries;
}



