import {
  getSeries
} from './chartsOptionsUtil'
import _color from './color'
export default (data) => {
  var years = data.yearList;
  var xAxis = data.cityList;
  var indexCityData = data.indexCityData;
  const title = data.title;
  const series = [];
  xAxis.forEach((e, i) => {
    let _data = indexCityData[e];
    const dd = [];
    for (const key in _data) {
      dd.push(_data[key] || '0');
    }
    series.push({
      name: e,
      type: 'line',
      data: dd,
      markPoint: {
        data: [{
            type: 'max',
            name: '最大值'
          },
          {
            type: 'min',
            name: '最小值'
          }
        ]
      },
      markLine: {
        data: [{
          type: 'average',
          name: '平均值'
        }]
      }
    })
  })
  return {
    // dataZoom: [{
    //     type: 'slider',
    //     show: true,
    //     xAxisIndex: [0]
    //   },
    //   {
    //     type: 'inside',
    //     xAxisIndex: [0]
    //   }
    // ],
    title: {
      text: title
    },
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      data: xAxis
    },
    toolbox: {
      show: true,
      right: 20,
      itemGap: 20,
      orient: 'vertical',
      feature: {
        // dataZoom: {
        //   yAxisIndex: 'none'
        // },
        // dataView: {
        //   readOnly: false
        // },
        magicType: {
          type: ['line', 'bar']
        },
        restore: {},
        saveAsImage: {}
      }
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: years
    },
    yAxis: {
      type: 'value',
      // axisLabel: {
      //   formatter: '{value} °C'
      // }
    },
    series
  }
}



