export const getColums = (data) => {
  let columns1 = [{
    title: '地区',
    key: 'contry',
    align: 'center',
    fixed: 'left',
    width: 100
  }];
  for (var key in data.indexNameList) {
    let tem = {
      title: data.indexNameList[key],
      align: 'center',
      children: []
    };
    data.yearList.forEach((item, index) => {
      tem.children.push({
        title: item + '年',
        key: item + data.indexNameList[key],
        align: 'center',
        width: 110
      });
    });
    columns1.push(tem);
  }
  return columns1;
}
const concatData = (data1, data2) => {
  let tD = Object.assign({}, data1)
  for (var k in data1) {
    for (var ke in data2[k]) {
      tD[k][ke] = data2[k][ke]
    }
  }
  return tD;
}
export const getDatas = (data) => {
  let datas = [];
  let fD = {};
  for (var k in data) {
    for (var ke in data[k]) {
      for (const e in data[k][ke]) {
        data[k][ke][e].forEach((item, ink) => {
          let pre = k + e;
          if (!(item.cityId in fD)) {
            fD[item.cityId] = {};
            fD[item.cityId].contry = item.cityName;
            fD[item.cityId][pre] = item.value || '暂无数据';
          } else {
            fD[item.cityId][pre] = item.value || '暂无数据';
          }
        })
      }
    }
  }
  for (var fK in fD) {
    datas.push(fD[fK]);
  }
  return datas;
}



