<template>
    <div>
        <template v-for="(item, index) in cityDataList">
            <div :key="index+40" v-if="index===0" class="btn-show-data">
                <Button @click="value4 = true" size="small" type="dashed">查看指标信息</Button>
            </div>

            <div class="anchor" :key="index+300">
              <h2 :id="'ID'+index">{{item.indexNameList[index]}}</h2>
            </div>
            <div :id="`linecharts${index}`" class="charts-box" :key="index"></div>

            <div :key="index+10" class="result-box">
                <h4>指标分析</h4>
                <div style="text-indent: 14px; color: #888; line-height: 1.5">
                    <span style="color: #000">{{item.resultData[index].indexName}}：</span>指标平均最高得分是<span style="color: #f65c5c;"> {{item.resultData[index].topScore}} 分</span>
                    <span style="color: #000">( {{item.resultData[index].topYear}} 年 ) </span>，平均最低得分是<span style="color: #f65c5c;"> {{item.resultData[index].lowScore}} 分</span>
                    <span style="color: #000">( {{item.resultData[index].lowYear}} 年 ) </span>，整体呈现<span style="color: #000">{{item.resultData[index].number>0?'上升':'下降'}}趋势。</span>
                </div>
                <h4>地区分析</h4>
                <template v-for="(k, h) in item.resultData[index].cityValue">
                    <div :key="h+100" style="text-indent: 14px; color: #888; line-height: 1.5">
                        <span style="color: #000">{{k.cityname}}：</span>指标最高得分是<span style="color: #f65c5c;"> {{k.topScore}} 分</span>
                        <span style="color: #000">( {{k.topYear}} 年 ) </span>，最低得分是<span style="color: #f65c5c;"> {{k.lowScore}} 分</span>
                        <span style="color: #000">( {{k.lowYear}} 年 ) </span>，整体呈现<span style="color: #000">{{k.number>0?'上升':'下降'}}趋势。</span>
                    </div>
                </template>
            </div>
        </template>
        <Drawer :closable="false" width="30" v-model="value4">
            <h2>指标信息</h2>
            <template v-for="(item,index) in chartListData">
                <div :key="index">
                    <template v-for="(i,k) in item.indexInfos">
                        <div :key="k+20" class="data-list">
                            <p class="data-text">指标名称：<span>{{i.indexName}}</span></p>
                            <p class="data-text">指标描述：<span>{{i.description}}</span></p>
                            <p class="data-text">评分标准：<span>{{i.evaluationStandards}}</span></p>
                        </div>
                    </template>  
                </div>
            </template>
        </Drawer>

        

    </div>
</template>

<script>
    import echarts from 'echarts'
    import 'echarts/map/js/china.js'
    import 'echarts/theme/macarons.js'
    import createOptions from '../data/lineChartsOptions.js'
    export default {
        data() {
            return {
                cityDataList:[],
                 value4: false,
                pStyle: {
                    fontSize: '16px',
                    color: 'rgba(0,0,0,0.85)',
                    lineHeight: '24px',
                    display: 'block',
                    marginBottom: '16px'
                }
            }
        },
        props: ['chartListData'],
        mounted() {
            this.init();
            this.$nextTick(() => {
                this.initData();
            })
        },
        methods: {
            initmap(data,i) {
                const node = document.getElementById(`linecharts${i}`);
                const myChart = echarts.init(node, 'macarons').setOption(createOptions(data))
            },
            init() {
                let yearList = []
                this.chartListData.forEach((e, i) => {
                    const len = e.indexNameList.length;
                    let arr = []
                    yearList = e.yearList
                    e.indexNameList.forEach((k, ink) => {
                        let obj = {}, arr1 = []
                        obj.indexName = k
                        obj.cityInfo = e.indexCityData[k]
                        e.cityList.forEach((m, n) => {
                            let obj1 = {}, arr2 = []
                            obj1.cityname = m
                            obj1.infoList = obj.cityInfo[m]
                            e.yearList.forEach((o, u) => {
                                let obj2 = {}
                                obj2.year = o
                                obj2.value = obj1.infoList[o]
                                arr2.push(obj2)
                            })
                            
                            obj1.valueYearInfo = arr2
                            arr1.push(obj1)
                        })
                        obj.cityValue = arr1
                        arr.push(obj)
                    })
                    arr.forEach((el, q) => {
                        let list = [], list1 = [], averageList = []
                        el.cityValue.forEach((t, l) => {
                            list.push(Number(t.valueYearInfo.filter(item => item.year === yearList[0])[0].value))
                            list1.push(Number(t.valueYearInfo.filter(item => item.year === yearList[yearList.length-1])[0].value))
                        })
                        let sum = 0, sum1 = 0
                        for(var i = 0; i < list.length; i++){
                            sum += list[i]
                        }
                        for(var i = 0; i < list1.length; i++){
                            sum1 += list1[i]
                        }
                        el.number = sum1-sum  // number
                        yearList.forEach((k, y) => {
                            let yearValue = [], p = {}
                            p.year = k
                            el.cityValue.forEach((t, l) => {
                                yearValue.push(Number(t.valueYearInfo.filter(item => item.year === k)[0].value))
                            })
                            let sumall = 0
                            for(var i = 0; i < yearValue.length; i++){
                                sumall += yearValue[i]
                            }
                            p.average = (sumall / yearValue.length).toFixed(2) 
                            averageList.push(p)
                        })
                        // console.log(averageList)
                        el.averageList = averageList
                        let newList = averageList.map(item => Number(item.average))
                        let topScore = newList.sort((a,b) => {return a-b})
                        el.lowScore = topScore[0]
                        let topCityList1 = averageList.filter(item => Number(item.average) === el.lowScore).map(item => item.year)
                        topScore.reverse()
                        el.topScore = topScore[0]  // topScore
                        let topCityList = averageList.filter(item => Number(item.average) === el.topScore).map(item => item.year)
                        el.topYear = topCityList.join('、')  // topYear
                        el.lowYear = topCityList1.join('、')  // lowYear
                        delete el.cityInfo                  
                        el.cityValue.forEach((z,x) => {
                            let infoList = z.valueYearInfo.map(item => Number(item.value))
                            el.cityValue[x].number = infoList[infoList.length-1]-infoList[0]
                            let topValue = infoList.sort((a,b) => {return a-b})
                            el.cityValue[x].lowScore = topValue[0]
                            let topCityList1 = z.valueYearInfo.filter(item => item.value === String(el.cityValue[x].lowScore)).map(item => item.year)
                            topValue.reverse()
                            el.cityValue[x].topScore = topValue[0]  // topScore
                            let topCityList = z.valueYearInfo.filter(item => item.value === String(el.cityValue[x].topScore)).map(item => item.year)
                            el.cityValue[x].topYear = topCityList.join('、')  // topYear
                            el.cityValue[x].lowYear = topCityList1.join('、')  // lowYear 
                            delete el.cityValue[x].infoList
                            delete el.cityValue[x].valueYearInfo
                        })
                        // console.log(el.cityValue)
                    })
                    // console.log(arr)
                    e.resultData = arr
                    e.indexNameList.forEach((k, ink) => {
                        this.cityDataList.push({
                            yearList:e.yearList,
                            cityList:e.cityList,
                            indexCityData:e.indexCityData[k],
                            title:k,
                            indexNameList: e.indexNameList,
                            resultData: e.resultData
                        })
                    })
                })
            },
            initData(){
                this.cityDataList.forEach((f,j)=>{
                    this.initmap(f,j)
                })
            }
        }
    }
</script>

<style scoped>
    .charts-box {
        width: 100%;
        min-height: 640px;
        margin-top: 20px;
        overflow: hidden;
        padding: 20px 24px 20px;
        border: 1px solid #ebeef5;
    }
    .result-box{
        box-sizing: border-box;
        padding: 10px 30px 0;
        margin-bottom: 40px;
    }
    .data-list{
        margin-bottom: 20px;
    }
    .data-text{
        font-weight: 700;
        font-size: 14px;
    }
    .btn-show-data{
        text-align: right;
        margin-bottom: 40px;
    }

</style>



