<template>
    <div class="g-in m-minh">
        <Tabs type="card" @on-click="clickTabs">
            <TabPane label="地区对比分析">
                <div class="time-checkbox-wrap">
                    <charts :chartListData="chartListData" v-if="isChart"></charts>
                </div>
            </TabPane>
            <TabPane label="时间序列分析">
                <div class="time-checkbox-wrap anchor-wrap">
                    <line-charts :chartListData="chartListData" v-if="isChart"></line-charts>
                </div>
            </TabPane>
            <TabPane label="表格">
                <div class="time-checkbox-wrap">
                    <tables :chartListData="chartListData" v-if="isChart"></tables>
                </div>
            </TabPane>
        </Tabs>
        <!-- 侧边楼层 -->
        <div class="show-anchor" v-if="showAnchor">
            <Anchor show-ink :scroll-offset="-160">
                <AnchorLink :href="'#'+i.id" :title="i.title" v-for="i in anchorList" :key="i.id"/>
            </Anchor>
        </div>
        <BackTop></BackTop>
    </div>
</template>

<script>
    import charts from './charts'
    import lineCharts from './line-charts'
    import tables from './tables'
    import Cookies from 'js-cookie'
    export default {
        components: {
            charts,
            tables,
            lineCharts
        }, 
        data() {
            return {
                chartListData: [],
                isChart: false,
                showAnchor: false,
                anchorList: []
            }
        },
        async created() {
            const params = JSON.parse(Cookies.get('chartInfo'))
            // console.log(params)
            await this.getChartInfo(params)
        },
        methods: {
            async getChartInfo(params) {
                const res = await this.$ajaxPost(this.projectName + '/indexData/finds.do', params)
                this.isChart = true
                this.chartListData.push(res)
                const indexNameList = res.indexNameList
                this.anchorHandle(indexNameList)
            },
            anchorHandle(data) {
                data.forEach((el,index) => {
                    let obj = {}
                    this.$set(obj, 'title', el)
                    this.$set(obj, 'id', 'ID'+index)
                    this.anchorList.push(obj)
                })
            },
            clickTabs(name) {
                this.showAnchor = name === 1
            } 
        }
    }
</script>

<style scoped>
    .m-minh {
        min-height: 600px;
        padding-top: 20px;
        overflow: hidden;
    }
    .show-anchor{
        position: fixed;
        top: 50%;
        right: 30px;
        transform: translateY(-50%);
    }
</style>



