<template>
    <div class="">
        <Table :columns="columns11" :data="data10" border height="500"></Table>
    </div>
</template>

<script>
    import {
        getColums,
        getDatas
    } from '../data/tablesDatasUtil.js'
    export default {
        data() {
            return {
                columns11: [],
                data10: []
            }
        },
        props: ['chartListData'],
        created() {
            this.initTablesDatas();
            // console.log(this.chartListData)
        },
        methods: {
            initTablesDatas() {
                const len = this.chartListData.length;
                let _data=[],_p={};
                for(var i=0;i<len;i++){
                    _data = _data.concat(this.chartListData[i].dataMap);
                }
                _data.forEach(k=>{
                    for(i in k){
                        if(!(i in _p)){
                            _p[i]=[];
                        }
                        _p[i].push(k[i]);
                    }
                })
                this.data10 = getDatas(_p);
                this.chartListData.forEach((e, index) => {
                    const c1 = getColums(e);
                    if (index !== 0) {
                        c1.shift();
                    }
                    this.columns11 = this.columns11.concat(c1);
                })
            }
        }
    }
</script>

<style scoped>

</style>



